@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  box-sizing: border-box;
}
body {
  background: linear-gradient(to right, #73b3cf, #44a1c6) !important;

  background: url(/delivery/static/media/bigBalloon.c49e2575.jpg);

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: "Fira Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
}

body,
html,
.App,
#root {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-inner {
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 0.5rem;
  border-radius: 15px;
}

.custom-control-label {
  font-weight: 400;
}

/* On screens that are 992px or less*/
@media screen and (max-width: 992px) {
}
.auth-inner {
  width: 75%;
}

/* On screens that are 600px or less*/
@media screen and (max-width: 600px) {
  .auth-inner {
    width: 90%;
  }
}

@media screen and (max-width: 300px) {
  .auth-inner {
    width: 100%;
  }
}

.App {
  background-image: url(/delivery/static/media/bigBalloon.c49e2575.jpg);
  min-height: 100%;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center;
  background-size: cover;
}

.App-link {
  color: #09d3ac;
}

.navbar {
  height: 7%;
  opacity: 0.8;
  -webkit-filter: (opacity=80);
          filter: (opacity=80);
}

ul.nav > li {
  display: inline-block;
  padding-left: 10px;
}

.nav {
  display: inline-block;
}

body {
  color: #553e65;
  font-weight: bolder;
  background-color: #edf0f2;
  margin-left: auto;
  margin-right: auto;
}

.Item__border {
  width: 100%;
  margin: 1vw 0;
  border-bottom: 10px solid;
  border-image-source: linear-gradient(45deg, #503ab7, #553e65);
  border-image-slice: 1;
}

/* Navbar */
.nav-link {
  color: #503ab7;
}

/* TaskItem */
.Item__card {
  background: #edf9f2;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  transition: all ease 500ms;
}

.Item__card-title {
  color: #3c565c;
}

.Item__button {
  background: #533c8e;
  box-shadow: 2px 1px 2px gray;
}

.complete {
  text-decoration: line-through;
}

.Item__card:hover {
  transform: scale(1.05);
}

.card-body {
  text-align: left;
}

.fa {
  color: #edf0f2;
}

/* Add Item */

.AddItem__card {
  background: #edf9f2;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
}

/* ListVolunteers */
.Volunteer__card {
  background-color: #a0b3c7;
  box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
  transition: all ease 500ms;
}

